@media (max-width: 768px)
{
    .bio
    {
    width: 10%;
    display: flex;
    flex-direction: column-reverse !important;
    }
    .name
    {
        text-align: left;
    }
    .bio_details
    {
        width: 10%;
    }
    .summary_text{
        width: 92%;
    }
}
.intro{
    width: 84%;
    margin: 10px auto;;
    display: flex;
    flex-direction: column;
    font-family:'Times New Roman', Times, serif;
    align-items: center;
    text-align: center;
}
.intro2{
    display: flex;
    flex-direction: column;
    margin: 5% 10%;
}
.download{
    margin-top: 3%;
    width:80%;
    color:black;
    background-color:  rgba(180, 201, 240, 0.979);
    padding: 10px;
    font-size: 18px;
    display: inline-block;
    border-radius: 20px;
    text-align: center;
}
.download:hover{
    background-color:rgba(202, 215, 240, 0.979);
    cursor:pointer;
}
.bio{
    width: 98%;
    margin-top: 15px;
    display: flex;
    background-color:rgba(218, 228, 245, 0.979);
    padding: 10px;
    margin-bottom: 30px;
}
.name{
    font-size: 24px;
    text-align: right;
    width: 1%;
    padding-right: 2px;
}
.bio_details{
    font-size: 10px;
    text-align: left;
    padding:3px ;
    width: 60%;
}
.linkedin{
    font-size: 8px;  
}
.summary_text{
    width: 96%;
    margin-top: 30px;
    margin: 5px auto;
    padding: 10px auto;
    text-align: justify;
    font-size: 18px;
    align-items: center
}
.resume_intro{
    margin: 5px auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: none;
    padding-top: none;
    color: rgba(0, 0, 0, 0.753);
}
.resume_parts{
    display: flex;
    text-align: justify;
}
.resume_parts_projects{
    display: flex;
    width: 96%;
    margin: 10px;
}
.resume_section{
    width: 48%;
    margin: 10px;
    text-align: justify;
}
.intro_left_style{
    background-color: rgba(180, 201, 240, 0.979);
    text-align: center;
    margin-top:20px;
    font-size: 20px;
    text-align: center;
}
.intro_left_style_projects{
    padding :2px 10px;
    height: auto;
    font-style: bold;
    font-size: 12px;
    text-align: justify;
}
.intro_left_education{
    text-align:left;
    font-size: 13px;
    text-align: justify;
}
.intro_right_style{
    background-color: rgba(180, 201, 240, 0.979);
    text-align: center;
    margin-top: 20px;
    font-size: 20px;
    text-align: center;
}
.intro_right_skills{
    text-align: left;
    width: auto;
    height: auto;
    font-size: 13px;
    text-align: justify;
}
.intro_right_skills_style{
    padding: 8px;
    border-radius: 5px;
    background-color:rgba(227, 234, 245, 0.979);
    margin: 6px;
    display:inline-flex;
}
.intro_right_skills_style2{
    font-size: 11px;
}
.resume_projects{
    width: 100%;
}
